import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import { API_HEADER, API_LINK } from "./ApiService";

export const sendEmail = createAsyncThunk(
  "mail/site_contact_mail_Controller",
  async ({ subject, email, message }, { rejectedWithValue }) => {
    try {
      const { data } = await axios.get(
        `${API_LINK}mail/site_contact_mail_Controller`,
        { subject: subject, email: email, message: message },
        { headers: API_HEADER }
      );
      return data.data;
    } catch (error) {
      return rejectedWithValue(error.message);
    }
  }
);
