import * as Yup from "yup";

export let CheckCertificateSchema = Yup.object().shape({
  code: Yup.string("صيغة الكود غير صحيحة").min(3).required(
    "الكود مطلوب"
  ),
});

export let ContactUsSchema = Yup.object().shape({
  subject: Yup.string("صيغة الكود غير صحيحة").required(
    "الموضوع مطلوب"
  ),
  email: Yup.string().email("صيغة البريد الالكتروني خاطئة").required(
    "البريدالالكتروني مطلوب"
  ),
  message: Yup.string("صيغة الكود غير صحيحة").required(
    "الرسالة مطلوبة"
  ),
});