import React from "react";
import { Col, Container, FloatingLabel, Form, Row } from "react-bootstrap";
import { useFormik } from "formik";
import { ContactUsSchema } from "../../util/validationSchema";
import { useDispatch } from "react-redux";
import MainButton from "../../components/shared/Buttons/MainButton";
import SectionTitle from "../../components/site/SectionTitle";
import { API_LINK, API_HEADER } from "../../services/ApiService";
import axios from "axios";
import { sendEmail } from "../../services/contact.service";

let ContactUs = () => {
  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: {
      subject: "",
      email: "",
      message: "",
    },
    validationSchema: ContactUsSchema,
    onSubmit: async (values) => {
      sendEmail({subject:values.subject, email:values.email, message:values.message}).catch((error) => {console.log(error);})
    },
  });

  return (
    <>
      <style>
        form
        {`{
            display: flex;
            flex-direction: column;
        }`}
        .form-control
        {`{
            font-size:18px;
        }`}
      </style>
      <section className="mt-5">
        <Container>
          <SectionTitle title={"تواصل معنا"} />
          <Row>
            <Col lg={6} md={12}>
              <Form onSubmit={formik.handleSubmit}>
                <Form.Group className="mb-3">
                  <FloatingLabel controlId="floatingInput" label="ادخل العنوان">
                    <Form.Control
                      type="text"
                      placeholder="subject"
                      name="subject"
                      onChange={formik.handleChange}
                      value={formik.values.subject}
                      isInvalid={!!formik.errors.subject}
                    />
                  </FloatingLabel>
                  <Form.Control.Feedback type="invalid">
                    {formik.errors.subject}
                  </Form.Control.Feedback>
                </Form.Group>
                <Form.Group className="mb-3">
                  <FloatingLabel
                    controlId="floatingInput"
                    label="ادخل بريدك الالكتروني"
                  >
                    <Form.Control
                      type="text"
                      placeholder="email"
                      name="email"
                      onChange={formik.handleChange}
                      value={formik.values.email}
                      isInvalid={!!formik.errors.email}
                      style={{ direction: "ltr" }}
                    />
                  </FloatingLabel>
                  <Form.Control.Feedback type="invalid">
                    {formik.errors.email}
                  </Form.Control.Feedback>
                </Form.Group>
                <Form.Group>
                  <FloatingLabel
                    controlId="floatingInput"
                    label="ما المشكلة التي تواجهك؟"
                  >
                    <Form.Control
                      as="textarea"
                      placeholder="message"
                      name="message"
                      style={{ height: "200px" }}
                      onChange={formik.handleChange}
                      value={formik.values.message}
                      isInvalid={!!formik.errors.message}
                    />
                  </FloatingLabel>
                  <Form.Control.Feedback type="invalid">
                    {formik.errors.message}
                  </Form.Control.Feedback>
                </Form.Group>
                {/* <Loading loading={loading} error={error}> */}
                <MainButton buttonText={"ارسال"} buttonType={"submit"} />
                {/* </Loading> */}
              </Form>
            </Col>
            <Col lg={6} md={12}>
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d356.55415704714164!2d36.71231642590482!3d34.73144971686381!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x15230f5401aecb0d%3A0xec4a5f4fd9eaf9a8!2sAssist%20Co.%20%26%20Training%20center!5e0!3m2!1sar!2snl!4v1681731317227!5m2!1sar!2snl"
                loading="lazy"
                style={{ width: "100%", height: "100%" }}
              ></iframe>
            </Col>
          </Row>
          <div className="or">
            <span></span>
            <span>أو</span>
            <span></span>
          </div>
          <div className="social-media">
            <a href="https://www.facebook.com/we.assist.it">
              <i className="fa-brands fa-facebook-f"></i>
            </a>
            <a href="https://wa.me/00963940245564">
              <i className="fa-brands fa-whatsapp"></i>
            </a>
            <a href="https://t.me/username">
              <i className="fa-brands fa-telegram"></i>
            </a>
            <a href="tel:+963996697938">
              <i className="fa fa-phone"></i>
            </a>
            <a href="mailto:assist@info.com">
              <i className="fa fa-envelope"></i>
            </a>
          </div>
        </Container>
      </section>
    </>
  );
};

export default ContactUs;
